import { css } from 'glamor'

const container = css({
  position: 'absolute',
  bottom: '2vw',
  margin: '0',
  height: '30%',
  display: 'flex',
  flexWrap: 'wrap',
  alignItems: 'center',
  justifyContent: 'center',
  width: '100vw',
})

export default {
  container,
}



